@media screen and (max-width: 768px) {

    .header { background-image: url(../images/header.768.jpeg); }
    
    .name { letter-spacing: 6px; }
    
    .first-name { font-size: 54px; }

    .last-name { font-size: 60px; }

    .line { width: 360px; }
    
    .front { font-size: 16px; }
    
    .link-content {
        &:hover { color: #888; }

        &:active { color: #333; }
    }
    
    .portfolio-item:nth-of-type(odd) {
        .work-image {
            float: none;
            margin-left: 0;
            margin-right: 0;
        }
    }
    .work-title {
        &:hover { color: #333; }
        
        &:active { color: $main-color; }
    }
    
    .btn {
        &:hover { background-color: $main-color; }

        &:active { background-color: #003459; }
    }
    .btn-inv {
        &:hover { background-color: #ddd; }

        &:active { background-color: #bbb; }
    }
    .work-image, .description-container {
        display: block;
        width: 100%;
    }
    
    .description-container { margin-top: 20px; }
    
    .about {
        padding: 20px 16%;
        background-image: url("../images/about.1024.jpg");
    }
}
